<template>
  <div ref="echart" class="commentContainer" ></div>
</template>

<script>
import * as echarts from "echarts";
import "echarts-wordcloud/dist/echarts-wordcloud";
import "echarts-wordcloud/dist/echarts-wordcloud.min";
export default {
  data() {
    return {
      charts: "",
      name:"",
      opinionData: [
        { value: 420, name: "" },
        { value: 1200, name: "" },
        { value: 780, name: "" },
        { value: 2350, name: "" },
        { value: 1548, name: "" },
        { value: 1025, name: "" },
        { value: 161, name: "" },
        { value: 5201, name: "" },
        { value: 750, name: "   " },
        { value: 189, name: "" },
        { value: 999, name: "" },
        { value: 52, name: "" },
        { value: 40, name: "" },
        { value: 105, name: "" },
        { value: 3, name: "" },
        { value: 3, name: "" },
        { value: 800, name: "" },
        { value: 579, name: "" },
        { value: 263, name: "" },
        { value: 953, name: "" },
        { value: 218, name: "" },
      ],
    };
  },
  methods: {
    drawWordCould() {
      this.charts = echarts.init(this.$refs.echart);
      this.charts.setOption({
        series: [
          {
            type: "wordCloud",
            //
            gridSize: 10,
            //
            sizeRange: [14, 60],
            rotationRange: [0, 0],
            textStyle: {
              normal: {
                color: function () {
                  return (
                    "rgb(" +
                    Math.round(Math.random() * 255) +
                    ", " +
                    Math.round(Math.random() * 255) +
                    ", " +
                    Math.round(Math.random() * 255) +
                    ")"
                  );
                },
              },
            },
            left: "center",
            top: "center",
            right: null,
            bottom: null,
            width: "200%",
            height: "200%",
            //
            data: this.opinionData,
          },
        ],
      });
    },
  },
  mounted(){
      this.$nextTick(function(){
        this.drawWordCould();
      })
  }
};
</script>
<style scoped>
.commentContainer {
  background: #fff;
  padding: 0px;
  height: 100%;
  overflow: hidden;
  text-align: center;
}
</style>


